/* height x width ratio */

.same-ratio { 
  position: relative;
}

.same-ratio::after {
  content: '';
  display: block;
  padding-top: 50%;  
}

.same-ratio-content {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.bgc{
  background-image: linear-gradient(to right, rgb(44, 60, 130), rgb(12, 11, 72));
}/*linear-gradient(to right, rgb(255, 210, 61), rgb(173, 120, 51))*/
.bgcc{background-image: linear-gradient(rgb(44, 60, 130), rgb(12, 11, 72), white, white);}


.cool-link {
  display: inline-block;
  color: #000;
  text-decoration: none;
}

.cool-link::after {
  content: '';
  display: block;
  width: 0;
  height: 4px;
  background: rgb(255, 255, 255);
  transition: width .3s;
}

.cool-link:hover::after {
  width: 100%;
}

.email-form{
  background-color: rgb(43, 54, 61);
