}
.email-form::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: lightblue;
  opacity: .8; /* Firefox */
}
.email-form:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: lightblue;
}
.email-form::-ms-input-placeholder { /* Microsoft Edge */
  color: lightblue;
}

